<template>
  <div>
    <div
      style="
        width: 100%;
        height: 3.5em;
        background-color: white;
        border-top: 1px solid rgb(233, 233, 233);
        display: flex;
        align-items: center;
      "
    >
      <div style="margin-left: 1em">
        <el-button-group style="">
          <el-button
            @click="handleButtonClick('/Enterprise')"
            :type="getButtonType('/Enterprise')"
            class="tab_button"
            >企业数据</el-button
          >
          <el-button
            @click="handleButtonClick('/Server')"
            :type="getButtonType('/Server')"
            >服务器数据</el-button
          >
          <el-button
            @click="handleButtonClick('/Provincial')"
            :type="getButtonType('/Provincial')"
            >省份数据</el-button
          >
        </el-button-group>
      </div>
    </div>
    <div style="border-top: 1px solid rgb(233, 233, 233)">
      <router-view></router-view>
    </div>
  </div>
</template>

<style>
.el-button-group {
  display: inline;
}
</style>

<script>
export default {
  data() {
    return {
      activeIndex: null,
    };
  },
  methods: {
    handleOpen() {
      console.log("Submenu opened");
    },
    handleClose() {
      console.log("Submenu closed");
    },
    handleButtonClick(path) {
      if (this.$route.path !== path) {
        this.$router.push(path);
      }
    },
    getButtonType(path) {
      return this.$route.path === path ? "primary" : "default";
    },
  },
};
</script>